@keyframes slide-in {
    from {
        transform: translateY(50%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.container {
    line-height: 24px;
    display: flex;
    border-style: solid none;
    border-width: 1px;

    animation: slide-in ease-out 0.15s;
}

.field {
    padding: 0 8px;
}

.rightField {
    text-align: right;
}
