@import "@moai/core/dist/theme/module.scss";

.stripe {
    @include light {
        background-image: repeating-linear-gradient(
            to bottom,
            transparent 0px,
            transparent 64px,
            var(--light-gray-1) 64px,
            var(--light-gray-1) 128px
        );
    }

    @include dark {
        background-image: repeating-linear-gradient(
            to bottom,
            transparent 0px,
            transparent 64px,
            var(--dark-gray-0) 64px,
            var(--dark-gray-0) 128px
        );
    }
}
