@import "@moai/core/dist/theme/module.scss";

.path {
    fill: none;
    stroke-width: 1px;
    stroke-linejoin: round;
    @include light {
        stroke: var(--light-gray-4);
    }
    @include dark {
        stroke: var(--dark-gray-4);
    }
}

.to {
    // Markers must be defined inside CSS in order to have better priority
    // when exporting to images
    // See: https://github.com/tsayen/dom-to-image/issues/125#issuecomment-480301690
    marker-end: url("#edge-to");
}

.from {
    // See ".to"
    marker-end: url("#edge-from");
}

:global(.temp-selected),
.selected {
    .path {
        @include light {
            stroke: var(--accent-2);
        }
        @include dark {
            stroke: var(--accent-2);
        }
        stroke-width: 2px;
    }
    .dot {
        fill: var(--accent-2);
    }
}

.hitarea {
    stroke-width: 20px;
    stroke: transparent;
    fill: transparent;
    pointer-events: stroke;
    cursor: pointer;
}
