.container {
    border-width: 1px;
    border-style: solid;
    z-index: 1;
}

.container[data-popper-escaped="true"],
.container[data-popper-reference-hidden="true"] {
    visibility: hidden;
    pointer-events: none;
}

.row {
    display: flex;
    align-items: center;
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}
