.container {
    border-style: solid;
    border-width: 1px;

    /* Drag */
    cursor: grab;

    /* Select */
    transition: outline 0.2s ease-out;
    outline: solid 8px transparent;
    outline-offset: -1px;
}

.container.react-draggable-dragging {
    cursor: grabbing;
}

/* "temp-selected" is the global class applied to the tables while the user is
dragging to select. See canvas/canvas/select/overlap */
:global(.temp-selected) .container,
.container.selected {
    outline: solid 2px var(--accent-2);
}

/* To keep the table's rows stay when expanded */
.container.expanded {
    /* Same value as the width of the side buttons of a row */
    margin-left: -28px;
    margin-right: -28px;
}

.body {
    position: relative;
}

.columns {
    position: absolute;
    /* 8px is from the div between header and body */
    bottom: calc(100% - 8px);
    left: 0;
    width: 100%;
    z-index: 1;
}
