@keyframes slide-in {
    from {
        transform: translateX(50%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.container {
    animation: slide-in ease-out 0.15s;
    color: var(--accent-negative);
}

.container button {
    transition: none;
}
