.container {
    padding: 8px;
    position: relative;
}

.box {
    padding: 8px;
}

@media (min-width: 1001px) {
    .container {
        display: flex;
        justify-content: space-between;
    }
}

@media (max-width: 1000px) {
    .container {
        display: block;
    }
}

@media (min-width: 601px) {
    .sub {
        display: flex;
        justify-content: space-between;
    }
}

@media (max-width: 600px) {
    .sub {
        display: block;
    }
}
