@keyframes slide-in {
    from {
        transform: translateX(-50%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.container {
    display: block;
    animation: slide-in ease-out 0.15s;
    /* Don't define "cursor" here. It should be in the button */
    color: var(--accent-positive);
}

.dragging {
    color: var(--accent-2);
}

/* Override the button cursor as we prefer the drag action over click */
.container button {
    cursor: grab;
    transition: none;
}
