.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    /* Above all tables, including selected ones */
    z-index: 2;

    color: var(--accent-2);
}

.dot {
    position: absolute;
}

.svg {
    width: 100%;
    height: 100%;
}

.line {
    stroke: currentColor;
    stroke-width: 2px;
}
