@import "@moai/core/dist/theme/module.scss";

.container {
    --light-gray-4: #c3c3c3;
    --dark-gray-4: #585858;
}

.main {
    @include light {
        background-color: #ffffff;
    }
    @include dark {
        background-color: #252525;
    }
}

.more {
    @include light {
        background-color: #f6f6f6;
    }
    @include dark {
        background-color: #333333;
    }
}
