.row {
    display: flex;
    align-items: center;
    padding: 0 8px;
}

.more {
    flex-wrap: wrap;
}

.search {
    flex: 1 1 0px;
}

.box {
    padding: 0 8px;
    height: 64px;
    display: flex;
    align-items: center;
}

@media (min-width: 1001px) {
    .main .boxView,
    .main .boxEdit {
        display: flex;
    }
    .more .boxView,
    .more .boxEdit {
        display: none;
    }
}

@media (min-width: 601px) and (max-width: 1000px) {
    .main .boxView,
    .more .boxEdit {
        display: flex;
    }
    .more .boxView,
    .main .boxEdit {
        display: none;
    }
}

@media (max-width: 600px) {
    .main .boxView,
    .main .boxEdit {
        display: none;
    }
    .more .boxView,
    .more .boxEdit {
        display: flex;
    }
}
