@import "@moai/core/dist/theme/module.scss";

.container {
    padding: 8px;
    position: relative;
}

.main {
    transition: transform 0.15s ease-out;
    text-align: center;
    font-weight: 600;
    flex: 0 0 auto;
}

.container.expanded {
    /* To keep the header name position when entering expanded mode.
    See table/visible/visible.css */
    padding-left: calc(8px + 28px);
    padding-right: calc(8px + 28px);
}

.container.expanded .main {
    transform: translateY(-8px);
}

.view {
    line-height: 24px; // Mimic text box
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    // mimic text-box
    &:hover {
        @include light {
            background-color: var(--light-gray-2);
        }
        @include dark {
            background-color: var(--dark-gray-2);
        }
    }
}