.container {
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.header {
    flex: 0 0 auto;
}
.footer {
    flex: 0 0 auto;
}
.body {
    flex: 1 1 0px;
    overflow: hidden;
    display: flex;
}
.canvas {
    flex: 1 1 0px;
    overflow: hidden;
    position: relative;
}
.library {
    flex: 0 0 auto;
    overflow: hidden;
    max-width: 80vw;
}
